<template>
  <nav>
    <div class="nav-wrap green">
      <div class="container">
        <router-link to="/" class="logo">BookApp</router-link>
        <ul class="right">
          <li v-if="isLoggedIn">
            <span class="email white-text">
              <span class="fa fa-user-o"></span>
              {{currentUser}}
            </span>
          </li>
          <li v-if="isLoggedIn">
            <router-link to="/">Dashboard</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/login">Login</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/register">Register</router-link>
          </li>
          <li v-if="isLoggedIn">
            <button v-on:click="logout" class="btn red">Logout</button>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import firebase from 'firebase';
export default {
  name: 'navbar',
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function () {
      firebase.auth().signOut().then(() => {
        this.$router.go({ path: this.$router.path });
      })
    }
  }
}
</script>


<style scoped>
.logo {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: bold;
}
.email {
  padding-right: 10px;
}
</style>




