<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col s12 m8 offset-m2">
          <div class="login card-panel green white-text center">
            <h3>Login</h3>
            <form>
              <div class="input-field">
                <i class="fa fa-user-circle-o prefix" aria-hidden="true"></i>
                <input class="white-text" type="text" id="email" v-model="email">
                <label class="white-text" for="email">Email</label>
              </div>
              <div class="input-field">
                <i class="fa fa-lock prefix" aria-hidden="true"></i>
                <input class="white-text" type="password" id="password" v-model="password">
                <label class="white-text" for="password">Password</label>
              </div>
              <button v-on:click="login" class="btn btn-large grey lighten-4 black-text">Login</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import firebase from 'firebase';
export default {
  name: 'login',
  date: function () {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    login: function (e) {
      firebase.auth().signInWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            alert(`You are logged in as ${this.email}`);
            this.$router.go({ path: this.$router.path });
          },
          err => {
            alert(err.message);
          });
      e.preventDefault();
    }
  }
};
</script>

<style scoped>
.input-field .prefix.active {
  color: #fff;
}

.input-field input:focus {
  border-bottom: 1px solid #fff !important;
  -webkit-box-shadow: 0 1px 0 0 #fff !important;
  box-shadow: 0 1px 0 0 #fff !important;
}
input,
body input:-internal-autofill-previewed,
input:-internal-autofill-selected,
textarea:-internal-autofill-previewed,
textarea:-internal-autofill-selected,
select:-internal-autofill-previewed,
select:-internal-autofill-selected {
  background-color: transparent !important;
}
</style>




