<template>
  <div id="edit-booking">
    <h3>Edit Booking</h3>
    <div class="row">
      <form @submit.prevent="updateBooking" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" disabled v-model="book_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <select class="select-dropdown dropdown-trigger" v-model="barber_name">
              <option value disabled selected>Choose your Barber</option>
              <option value="Barber01">Barber01</option>
              <option value="Barber02">Barber02</option>
              <option value="Barber03">Barber03</option>
            </select>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" class="datepicker" v-model.lazy="date" required>
          </div>
        </div>
        <button type="submit" class="btn submit">Save</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>


<script>
import db from './firebaseinit'
export default {
  name: "edit-booking",
  data() {
    return {
      book_id: null,
      name: null,
      barber_name: null,
      date: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('bookings').where('book_id', '==', to.params.app_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.book_id = doc.data().book_id
          vm.name = doc.data().name
          vm.barber_name = doc.data().barber_name
          vm.date = doc.data().date
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('bookings').where('book_id', '==', this.$route.params.app_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.book_id = doc.data().book_id
          this.name = doc.data().name
          this.barber_name = doc.data().barber_name
          this.date = doc.data().date
        })
      })
    },
    updateBooking() {
      db.collection('bookings').where('book_id', '==', this.$route.params.app_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            book_id: this.book_id,
            name: this.name,
            barber_name: this.barber_name,
            date: this.date
          })
            .then(() => {
              this.$router.push({ name: 'view-booking', params: { app_id: this.book_id } })
            })
        })
      })
    }
  },
  mounted: function () {
    $(document).ready(function () {
      $("select").formSelect();
      $(".datepicker").datepicker({
        autoClose: "true"
      });
    })
  }
}
</script>



