<template>
  <div id="view-booking">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Booking ID: {{book_id}}</li>
      <li class="collection-item">Name: {{name}}</li>
      <li class="collection-item">Barber Name: {{barber_name}}</li>
      <li class="collection-item">Appointment Date: {{date}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteBooking" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{name: 'edit-booking', params: {app_id: book_id}}"
        class="btn-floating btn-large red"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>


<script>
import db from './firebaseinit'
export default {
  name: "view-booking",
  data() {
    return {
      book_id: null,
      name: null,
      barber_name: null,
      date: null
    }
  },
  beforeRouteEnter(to, from, next) {
    db.collection('bookings').where('book_id', '==', to.params.app_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.book_id = doc.data().book_id
          vm.name = doc.data().name
          vm.barber_name = doc.data().barber_name
          vm.date = doc.data().date
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('bookings').where('book_id', '==', this.$route.params.app_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.book_id = doc.data().book_id
          this.name = doc.data().name
          this.barber_name = doc.data().barber_name
          this.date = doc.data().date
        })
      })
    },
    deleteBooking() {
      if (confirm('Are you sure to delete this booking?')) {
        db.collection('bookings').where('book_id', '==', this.$route.params.app_id).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>



