<template>
  <div id="new-booking">
    <h3>New Booking</h3>
    <div class="row">
      <form @submit.prevent="saveBooking" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="book_id" required>
            <label>Booking ID</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <select class="select-dropdown dropdown-trigger" v-model="barber_name">
              <option value disabled selected>Choose your Barber</option>
              <option value="Barber01">Barber01</option>
              <option value="Barber02">Barber02</option>
              <option value="Barber03">Barber03</option>
            </select>
            <label>Materialize Select</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" class="datepicker" v-model.lazy="date" required>
            <label>Date</label>
          </div>
        </div>
        <button type="submit" class="btn submit">Add</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>


<script>
import db from './firebaseinit'
export default {
  name: "new-booking",
  data() {
    return {
      book_id: null,
      name: null,
      barber_name: null,
      date: null
    }
  },
  mounted: function () {
    $(document).ready(function () {
      $("select").formSelect();
      $(".datepicker").datepicker({
        autoClose: "true"
      });
    })
  },
  methods: {
    saveBooking() {
      db.collection('bookings').add({
        book_id: this.book_id,
        name: this.name,
        barber_name: this.barber_name,
        date: this.date
      })
        .then(docRef => this.$router.push('/'))
        .catch(error => console.log(err))
    }
  }
}
</script>


