<template>
  <div id="dashboard">
    <h3>Dashboard</h3>
    <ul class="booking-container">
      <li class="collection-header">
        <h4>Bookings</h4>
      </li>
      <li v-for="booking in bookings" v-bind:key="booking.id" class="collection-item">
        <div class="card">
          <div class="card-title teal darken-3">
            <h4>{{booking.name}}</h4>
            <router-link
              class="secondary-content btn-floating halfway-fab waves-effect waves-light red"
              v-bind:to="{name: 'view-booking', params: {app_id: booking.book_id}}"
            >
              <i class="fa fa-eye"></i>
            </router-link>
          </div>
          <div class="card-content">
            <span class="date">{{booking.date}}</span>
            <span class="barber" data-badge-caption>{{booking.barber_name}}</span>
          </div>
        </div>
      </li>
    </ul>
    <!--<ul class="collection with-header">
      <li class="collection-header">
        <h4>Bookings</h4>
      </li>
      <li v-for="booking in bookings" v-bind:key="booking.id" class="collection-item">
        <div class="chip">{{booking.book_id}}</div>
        <span>{{booking.name}}</span>
        <span class="badge">{{booking.date}}</span>
        <span class="new badge blue" data-badge-caption>{{booking.barber_name}}</span>

        <router-link
          class="secondary-content"
          v-bind:to="{name: 'view-booking', params: {app_id: booking.book_id}}"
        >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>-->
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>


<script>
import db from './firebaseinit'
export default {
  name: "dashboard",
  data() {
    return {
      bookings: []
    }
  },
  created() {
    db.collection('bookings').orderBy('date').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'book_id': doc.data().book_id,
          'name': doc.data().name,
          'barber_name': doc.data().barber_name,
          'date': doc.data().date
        }
        this.bookings.push(data)
      })
    })
  }
}
</script>


<style>
.card-title {
  padding: 15px 24px;
}

.card-title h4 {
  margin: 0;
  text-transform: uppercase;
  color: #fff;
}
.booking-container {
  display: flex;
  flex-wrap: wrap;
}

.booking-container li {
  width: 25%;
  box-sizing: border-box;
  padding: 10px;
}

.booking-container .collection-header {
  width: 100%;
}

.card-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 35px !important;
}
.card-content .date {
  font-size: 20px;
}

.card-content .barber {
  font-size: 20px;
}

@media screen and (max-width: 1400px) {
  .booking-container li {
    width: 33.333%;
  }
}

@media screen and (max-width: 1200px) {
  .booking-container li {
    width: 50%;
  }
}

@media screen and (max-width: 768px) {
  .booking-container li {
    width: 100%;
  }
}
</style>



